/* src/App.css */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f7f7f7;
}

.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 30px;
}

@media (max-width: 768px) {
  .App {
    padding: 15px;
  }

  h1 {
    font-size: 24px;
  }
}

.team-members {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  padding: 30px;
}

.team-member {
  background-color: white;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 240px;
  min-height: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-member img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin-top: -60px;
  border: 5px solid white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.team-member h3 {
  margin: 8px 0;
  font-size: 20px;
  font-weight: bold;
}

.team-member p {
  margin: 0;
  margin-bottom: 16px; /* 이 부분을 추가하세요 */
  font-size: 14px;
  color: #666;
}
.team-member p:last-child {
  margin-bottom: 0; /* 마지막 문단에 대한 간격을 제거하세요 */
}
a {
  display: inline-block;
  padding: 10px 20px;
  background-color: #1e90ff;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  margin-top: 20px;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 480px) {
  .team-member {
    width: 100%;
    min-height: auto;
  }
}
